@media (min-width: 1200px){
  body{
  /* background: radial-gradient(circle, rgba(5,56,107,1) 10%, rgba(2,0,36,1) 100%, rgba(0,212,255,1) 100%); */
  background: radial-gradient(circle, rgba(34,193,195,1) 40%, rgba(15,103,204,1) 72%, rgba(29,169,168,1) 89%, rgba(34,195,154,1) 100%, rgba(34,193,195,1) 100%);
  text-align: center;
}
#intro
{
  color:    rgba(255,215,0,0.9);
 margin-top: 30px;
 font-size: 50px;
 font-weight: bold;
 text-shadow: 1px 1px 1px  red;
  
}
#content
{



  background: linear-gradient(0deg, rgba(34,195,154,0.4219887784215248) 34%, rgba(24,92,75,0.5088235123150823) 78%, rgba(195,173,34,0.5228291145559787) 100%);
  /* background: linear-gradient(0deg, rgba(34,193,195,1) 0%, rgba(253,187,45,1) 100%); */
  width: 80%;
  display: inline-block;
  margin-top: 70px;
  margin-bottom: 20px;
  border-radius: 10px;
}
#malelist
{
  float: left;
  width: 200px;
  height: 200px;
  text-align: center;
}
#malelist select,#femalelist select
{
     border-radius: 10px; 
     height: 30px;
}
#femalelist
{
  float: right;
  width: 200px;
  height: 200px;
  text-align: center;
}
#dd-list
{
  margin-top: 20px;
  margin-bottom: 50px;
  width: 200px;
  border-radius: 10px;
  height: 35px;
  padding-left: 5px;
}
#alert
{
  height: 40px;
  text-align: center;
  clear:both;
  align-self: center;
  background-color: rgba(0,0,0,0.2);
}
p
{
  font-weight: bold;
  font-size: 15px;
  padding-top: 10px;
}
.listIM
{
  color: deepskyblue;
  font-weight: bolder;
  text-shadow: 1px 1px 1px black;
}
.listIF
{
  color: pink;
  font-weight: bolder;
  text-shadow: 1px 1px 1px black;
}
#mM
{
  font-size: 24px;
  float: left;
  margin-left: 200px;
}
#mM button
{
  background-color: white;
  color:black;
  padding:10px;
  border: 3px solid grey;
  border-radius: 5px;
}

#mF
{
  font-size: 24px;
  float: right;
  margin-right: 200px;
}
#mF button
{
  background-color: white;
  color:black;
  padding:10px;
  border: 3px solid grey;
  border-radius: 5px;
}
#displaybox
{
  clear: both;
}
.email a
{
  text-decoration: none;
  color: darkgreen;
}
.email a:hover 
{
  color: black;
}






#cright
{
  color: black;
  font-weight: bold;
  margin-top: 30px;
  margin-bottom: 20px;
}

}




